*{
  background-color: lemonchiffon;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.btn {
  width: 80px;
  height: 20px;
  /* font-size: 1.2rem; */
  background: #ff005c;
  border: none;
  margin: 20px;
  outline: 2px solid #ff005c;
  outline-offset: 4px;
  border: 2px solid #ff005c;
  cursor: pointer;
  color: white;
  transition: all 250ms;
}

.btn:hover {
  outline-offset: -3px;
  color: rgb(34, 34, 34);
  background: none;

}
.text {

  border: 2px solid #ff005c;
  
}
.task {
  margin: auto;
  width: 25%;
  border: 1px solid #ff005c;
  color: black;
  background-color: whitesmoke;
}

.btn1 {

  background: #ff005c;
  border: none;
  margin: 10px;
  outline: 2px solid #ff005c;
  outline-offset: 4px;
  border: 2px solid #ff005c;
  cursor: pointer;
  color: white;
  transition: all 250ms;
}